body,
h1,
p, 
div,
button,
input,
span
 {
  margin: 0;
}

.wrapper_block {
    width:1440px;
    position: absolute;
    height: 80px;
    left: 0%;
    right: 0%;
    top: 0px;
    background: #FFFFFF;
}
.title:hover {
    border: 1px solid #52C41A;
    color:#52C41A;
    text-align: center; 
    border-radius: 4px;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.title {
    position: absolute;
    width: 147px;
    height: 56px;
    left: 22px;
    top: 9px;   
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    padding: 14px 5px 5px 5px;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 0 auto;
    color: rgba(0, 0, 0, 0.85);
}
.SignInBtn:hover{
    border: 1px solid #1890ff !important;
    border-radius: 4px;
    color:#1890ff ;
}
.SignInBtn {
    position: absolute;
    width: 84px;
    height: 35px;
    left: 1206px;
    top: 22px;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    align-items: center;
    justify-content: center;
    color: rgba(0, 0, 0, 0.85); 
    background-color: #FFFFFF; 
    border: none;
    outline: none;
    text-align: center;
    padding-left: 5px;
}
.SignUpBtn {
    position: relative;
    width: 109px;
    height: 51px;
    float: right;
    margin-right: 22px;
    top: 16px;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px; 
    text-align: center;
    color: #52C41A;
    border-radius: 5px;
    border: 1px solid #52C41A;
    background-color: #FFFFFF; 
}
.withAuth_block{
    position: relative;
    float: right;
    min-width: 400px;
    height: 80px;
    margin-right: 22px;
}
.createArticleBtn {
    position: relative;
width: 112.23px;
height: 31.1px;
top: 25px;
float: left;
margin-right: 27px;
color:#52C41A ;
border: 1px solid #52C41A;
box-sizing: border-box;
border-radius: 5px;
background-color: white;
}
.userName {
    position: relative;
height: 28px;
min-width: 10px;
top: 28px;
font-family: Inter serif;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 28px;
float: left;
margin-right: 12px;
display: flex;
align-items: flex-end;
color: rgba(0, 0, 0, 0.85);
}
.userAva {
    position: relative;
width: 46px;
height: 46px;
top: 19px;
margin-right: 27px;
float: left;
border-radius: 50%;
}
.logOutBtn {
    position: relative;
    width: 109px;
    height: 51px;
    top: 16px;
    float: left;
position: relative;
border: 1px solid rgba(0, 0, 0, 0.75);
box-sizing: border-box;
border-radius: 5px;
}
